.fourthCol {
  text-align: left;
  list-style: none;
}

.fourth-ul {
  list-style: none;
}

.footer-links {
  list-style: none;
  text-decoration: none;
  color: whitesmoke;
  cursor: pointer;
  font-size: 1.05rem;
}

.footer-links:hover {
  color: #C0C0C0;
}

.social-icons {
  padding-top: 17px;
}

.icon1 {
  margin-right: 10px;
  color: #f70;
  font-size: 1.2rem;
}

.firstIcons {
  padding-left: 25px;
}

.icons {
  margin: 0 13px 0 3px;
  font-size: 1.3rem;
}

.copyright {
  text-align: center;
  font-size: 1.1rem;
}

.footer-img {
  color: white;
  padding: 5% 0;
  text-align: center;
}


@media (max-width: 778px) {
  .fourthCol {
    padding-top: 10px;
  }

  .email {
    word-break: break-all
  }

  .icon1 {
    margin: 10px 3px 0 0;
    color: #f70;
    overflow-wrap: break-word;
  }

  .footer-links {
    font-size: 1rem;
  }

  .fourth-ul {
    padding-left: 0;
  }

  .contacts {
    float: left;
    width: 60%;
  }

  .social-icons-container {
    float: right;
    /* width: 20%; */
    width: 40%;
  }

  .firstIcons {
    padding-left: 0;
  }
}