.navL {
    color: #6c757d;
    font-size: 18px;
}

.navL:hover {
    color: white;
    background-color: #aea8f3;
    border-radius: 30px;
    box-shadow: 0 12px 15px 0 rgba(0, 0, 0, 0.24),
        0 17px 50px 0 rgba(0, 0, 0, 0.19);
}

.readMore {
    text-decoration: none;
    color: rgb(25, 133, 25);
}

.post:hover {
    background-color: #ededf1;
    /* border-radius: 10px; */
    box-shadow: 0 10px 21px 0 rgba(0, 0, 0, 0.24),
        0 7px 10px 0 rgba(0, 0, 0, 0.19);
    top: -8px;
    left: -7px;
    width: auto;
}


/* side Post---Side Post--- */
.side {
    height: 100px;
    margin-bottom: 5%;
}

.side-img {
    float: left;
    width: 40%;
    height: 100px;
    margin-right: 5%;
}

.side-body {
    margin-left: 90px;
}

.side-cat {
    font-size: 0.95rem;
    background-color: #82bce9;
    margin-right: 30px;
    color: white;
    padding: 4px 0;
    margin-bottom: 10px;
    margin-left: 20%;
}

.side-title {
    margin-bottom: 0;
    font-weight: 600;
}

.side-date {
    font-size: 0.7rem;
    margin-bottom: 0;
    color: #6c757d;
}


@media (min-width: 777px) {
    .side-cat {
        margin-left: 10px;
    }

}


@media (max-width: 778px) {

    /* ---Contact Us---Contact Us--- */
    .secondCol {
        margin-top: 15px;
    }

    .side {
        height: 90px;
        margin-bottom: 5%;
    }

    .side-img {
        width: 50%;
        height: 70px;
    }

    .side-body {
        margin-left: 50px;
    }

    .side-cat {
        font-size: 0.75rem;
        font-weight: 300;
        margin-right: 0px;
        margin-bottom: 5px;
        white-space: nowrap;
        margin-left: 10px;
    }

    .side-title {
        margin-bottom: 0;
        font-weight: 600;
        font-size: 0.7rem;
        white-space: nowrap;
    }

    .side-date {
        font-size: 0.5rem;
        margin-bottom: 0;
        color: #1c1f22;
    }
}