/* SignUp--- SignUp */
.signUp-con {
    width: 47%;
    text-align: 'left';
    padding-bottom: 10%;
}
.signUp-text{ 
    font-size: 1.13rem;
    font-weight: 500;
}
.signIn-text{
    font-size: 1.13rem;
    font-weight: 500;
}
.signIn{
    font-weight: 600;
    font-size: 1.16rem;
    text-decoration: none;
    color: rgb(217, 205, 197);
}
.signIn:hover{
    color: rgb(127, 57, 13);
}


/* SignIn---SignIn--SignIn */
.signInMain{
    width: 40%; 
    text-align: left; 
}


@media (max-width: 778px) {
    /* SignUp--- SignUp */
    .signUp-con {
        width: 65%;
    }
    .signUp-text{
        font-size: 1rem;
        font-weight: 500;
    }


    /* SignIn---SignIn--SignIn */
    .signInMain{
        width: 60%; 
    } 

}