/* Upper carousel */
.title {
    font-family: 'Beau Rivage', cursive;
    font-size: 79px;
    line-height: 100%;
    font-weight: 500;
}

.sideTitle {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    background-color: aqua;
}

.s-share {
    font-family: 'Courier New', Courier, monospace;
    font-size: 15px;
}

.b-date {
    font-size: 1.25rem;
}

.date {
    font-size: 12px;
    font-weight: bold;
}

.social-link {
    font-size: 14px;
    margin-left: 5rem;
}

.containe {
    position: relative;
    width: 100%;
}

.image {
    display: block;
    width: 100%;
    height: 50rem;
}

.imageOverlay {
    position: absolute;
    overflow: hidden;
    transition: .5s ease;
    bottom: 0;
    left: 100%;
    right: 0;
    width: 0;
    height: 50rem;
}
/* 
.containe:hover .imageOverlay {
    width: 100%;
    left: 0;
} */

.imageOverlayTwo {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    white-space: nowrap;
}

/* ---Blog---Blog--- */
.blog-container {
    height: 58rem;
    margin-left: 50px;
    padding-bottom: 75px;
}

.blogImage {
    height: 70%;
    width: 100%;
}

.excerpt {
    margin-bottom: 1.5rem;
}

.b-share {
    font-size: 1.527rem;
    font-weight: bold;
}

.b-icons {
    margin: 0 13px 0 3px;
    font-size: 1.3rem;
}

@media (max-width: 778px) {

    /* ---Top Carousel--- */
    .image {
        height: 15rem;
    }

    .imageOverlay {
        height: 15rem;
    }

    /* ---Blog---Blog--- */
    .blog-container {
        height: 32rem;
        margin-left: 30px;
        padding-bottom: 20px;
    }

    .b-date {
        font-size: 0.9rem;
    }

    .title {
        font-family: 'Beau Rivage', cursive;
        font-size: 57px;
        line-height: 100%;
        font-weight: 500;
    }

    .blogImage {
        height: 10rem;
    }

    .excerpt {
        margin-bottom: 0.5rem;
    }

    .b-share {
        font-size: 1rem;
        font-weight: bold;
    }

    .first-b-icons {
        padding-left: 10px;
    }

    .b-icons {
        margin: 0 10px 0 2px;
        font-size: 0.9rem;
    }

    .home-read {
        font-size: 0.7rem;
    }
}

/* ---SidePost---SidePost--- */
.s-share {
    font-family: 'Courier New', Courier, monospace;
    font-size: 11px;
}

.social-link {
    font-size: 11px;
    margin-left: 0.3rem;
}