* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    }

    /* Jumbo---Jumbo---Jumbo */
    .text-content{
        position: absolute; 
        top: 28%;
        right:40%;
        /* bottom: 40%; */
        translate: middle;
    }
    .jumbo-title{
        font-family: 'Courier New', Courier, monospace;
        font-size: 80px;
        font-style: oblique;
        line-height: 100%;
        /* font-weight: 500; */
    }
    .jumbo-author{
        /* font-family: 'Courier New', Courier, monospace; */
        font-family: 'Beau Rivage', cursive;
        font-size: 36px;
    }
    .author-name{
        font-family: 'Courier New', Courier, monospace;
        /* font-family: 'Beau Rivage', cursive; */
        font-style: oblique;
        font-size: 25px;
    }
